img
{
    width: 200px !important;
    height: 220px !important;
    -webkit-box-shadow: 7px 7px 3px rgba(37, 36, 36, 0.5);
            box-shadow: 7px 7px 3px rgba(37, 36, 36, 0.5);
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; 
}

img:hover{
    cursor: pointer;
    -webkit-box-shadow: 12px 12px 6px rgba(63, 59, 59, 0.5);
            box-shadow: 12px 12px 6px rgba(63, 59, 59, 0.5);
    -webkit-transform: scale(1.11, 1.11);
        -ms-transform: scale(1.11, 1.11);
            transform: scale(1.11, 1.11);
} 


